import './App.css'
import Navbar from "./components/Navbar";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from './components/pages/Home'
import Services from './components/pages/Services';
import Product from './components/pages/Product';
import Signup from './components/pages/SignUp';

function App() {
  return (
    <>
      <Router>
        <Navbar />
        <Switch>
          <Route path="/react-travel-pages" exact component={Home}/>
          <Route path="/react-travel-pages/services" component={Services}/>
          <Route path="/react-travel-pages/products" component={Product}/>
          <Route path="/react-travel-pages/sign-up" component={Signup}/>
        </Switch>
      </Router>
    </>
  );
}

export default App;
